"use client";

import Image from "next/image";

export type Product = {
  _id: string;
  name: string;
  description: string;
  price: number;
  inStock: boolean;
  imageUrl?: string;
};

type Props = {
  product: Product;
  onViewDetails: () => void;
};

export function ProductCard({ product, onViewDetails }: Props) {
  return (
    <div className="flex flex-col rounded-lg bg-white shadow-sm border border-zinc-200 p-4">
      <div className="relative mb-3 h-28 w-full overflow-hidden rounded-md bg-zinc-100">
        <Image
          src={product.imageUrl || "/window.svg"}
          alt={product.name}
          fill
          className="object-cover"
        />
      </div>
      <div className="text-sm font-semibold text-zinc-900 mb-1">
        {product.name}
      </div>
      <div className="text-xs text-emerald-600 font-semibold mb-3">
        ${product.price.toFixed(2)}
      </div>
      <button
        onClick={onViewDetails}
        className="mt-auto inline-flex items-center justify-center rounded-md border border-zinc-300 px-3 py-1 text-xs font-medium text-zinc-800 hover:bg-zinc-50"
      >
        View Details
      </button>
    </div>
  );
}

